import React from 'react';
import './App.css';

import { Navigation } from './components/Navigation';
import { Home } from './components/Home';
import { TaskManagement } from './components/TaskManagement';
import { EquipmentMonitoring } from './components/EquipmentMonitoring';
import { OrganisationManagement } from './components/OrganisationManagement';
import { Analytics } from './components/Analytics';
import { UserManual } from './components/UserManual';
import { SecurityFeatures } from './components/SecurityFeatures';
import { MeetTheTeam } from './components/MeetTheTeam';
import { TechnologyStack } from './components/TechnologyStack';
import { StyleGuide } from './components/StyleGuide';

import 'bootstrap/dist/css/bootstrap.min.css';

import { BrowserRouter, Route, Switch } from 'react-router-dom';

function App() {
  return (
    <BrowserRouter>
      <Navigation />
      <Switch>
        <Route path='/' component={Home} exact />
        <Route path='/task-management' component={TaskManagement} exact />
        <Route
          path='/equipment-monitoring'
          component={EquipmentMonitoring}
          exact
        />
        <Route
          path='/organisation-management'
          component={OrganisationManagement}
          exact
        />
        <Route path='/analytics' component={Analytics} exact />
        <Route path='/user-manual' component={UserManual} exact />
        <Route path='/security-features' component={SecurityFeatures} exact />
        <Route path='/meet-the-team' component={MeetTheTeam} exact />
        <Route path='/technology-stack' component={TechnologyStack} exact />
        <Route path='/style-guide' component={StyleGuide} exact />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
